<template>
	<el-row>
		<el-row style="padding: 150px 0px;background-color: transparent;">
			<el-dialog class="jxcja" :title="language[language.default].Reducestacking" :visible.sync="withdrawDialogData.Bool">
				<el-row>
					<span>{{language[language.default].Reduce}} {{withdrawDialogData.Pool.stakedToken.symbol}} {{language[language.default].stacking}}</span>
					<el-row class="fQZjeK">
						<input v-model="withdrawDialogData.Input" min="0.0" class="input_0" placeholder="0.0"
							style="text-align: right;" />
					</el-row>
					<el-row type="flex" justify="end">
						<span>{{language[language.default].Balance}}:{{withdrawDialogData.Pool.userInfo!=null?utils.TokenAmount(withdrawDialogData.Pool.stakedToken,withdrawDialogData.Pool.userInfo.amount).toSignificant(30):"0"}}</span>
					</el-row>
					<el-row style="margin: 20px;">
						<el-slider class="slider-ratio" v-model="withdrawDialogData.value" @input="withdrawDialogChange"
							:show-tooltip="false" :marks="marks">
						</el-slider>
					</el-row>
					<el-row>
						<button @click="withdraw" class="but0">
							{{language[language.default].Confirm}}
						</button>
					</el-row>
				</el-row>
			</el-dialog>
			<el-dialog class="jxcja" :title="language[language.default].Plusstacking" :visible.sync="depositDialogData.Bool">
				<el-row>
					<span>{{language[language.default].Plus}} {{depositDialogData.Pool.stakedToken.symbol}} {{language[language.default].stacking}}</span>
					<el-row class="fQZjeK">
						<input v-model="depositDialogData.Input" min="0.0" class="input_0" placeholder="0.0"
							style="text-align: right;" />
					</el-row>
					<el-row type="flex" justify="end">
						<span>{{language[language.default].Balance}}:{{depositDialogData.Pool.stakedToken!=null&&account.balances[depositDialogData.Pool.stakedToken.address]?utils.TokenAmount(depositDialogData.Pool.stakedToken,account.balances[depositDialogData.Pool.stakedToken.address]).toSignificant(30):0}}</span>
					</el-row>
					<el-row style="margin: 20px;">
						<el-slider class="slider-ratio" v-model="depositDialogData.value" @input="depositDialogChange"
							:show-tooltip="false" :marks="marks">
						</el-slider>
					</el-row>
					<el-row>
						<button @click="deposit" class="but0">
							{{language[language.default].Confirm}}
						</button>
					</el-row>
				</el-row>
			</el-dialog>
			<el-row style="padding: 0px 32px;">
				<el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
					<el-row>
						<el-row>
							<span style="font-size: 48px;font-weight: 500;color: rgb(153, 51, 153);">{{language[language.default].SyrupPool}}</span>
						</el-row>
						<el-row>
							<span style="font-size: 20px;font-weight: 500;color: rgb(153, 51, 153);">{{language[language.default].q40}}<br>{{language[language.default].q41}}</span>
						</el-row>
					</el-row>
				</el-col>
				<el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
					
				</el-col>
			</el-row>
			<el-row :gutter="20" style="margin: 0px 5px;">
				<el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8" v-for="pool in pools" v-bind:key="pool.address" style="margin-top: 30px;">
					<el-card shadow="never" style="border-radius: 40px;">
						<el-row slot="header" style="color: #993399;">
							<el-col :span="16">
								<el-row style="margin: 10px 0px;">
									<span style="font-size: 24px;font-weight: 600;">{{language[language.default].Earn}} {{pool.rewardToken.symbol}}</span>
								</el-row>
								<el-row style="margin: 10px 0px;">
									<span style="font-size: 20px;color: rgb(155 154 155);">{{language[language.default].Stacking}}
										{{pool.stakedToken.symbol}}</span>
								</el-row>
							</el-col>
							<el-col :span="8">
								<el-row type="flex" justify="end">
									<img style="border-radius: 32px;border: 1px solid rgba(0, 0, 0, 0.4);" width="64"
										height="64" :src="pool.rewardToken.img" />
									<img style="border-radius: 16px;border: 1px solid rgba(0, 0, 0, 0.4);margin-top: 40px;margin-left: -20px;"
										width="32" height="32" :src="pool.stakedToken.img" />
								</el-row>
							</el-col>
						</el-row>
						<el-row style="color: #993399;">
							<el-row type="flex" align="middle" style="margin: 10px 0px;">
								<el-col :span="10">
									<span style="font-size: 18px;font-weight: 400;">
										{{language[language.default].APY}}:
									</span>
								</el-col>
								<el-col :span="14">
									<el-row type="flex" justify="end">
										<span style="font-size: 18px;font-weight: 400;">
											{{pool.apr}}%
										</span>
									</el-row>
								</el-col>
							</el-row>
							<el-row type="flex" align="middle" style="margin: 10px 0px;">
								<el-col :span="10">
									<span style="font-size: 18px;font-weight: 400;white-space:nowrap;">
										{{language[language.default].Totalstacking}}:
									</span>
								</el-col>
								<el-col :span="14">
									<el-row type="flex" justify="end">
										<span style="font-size: 18px;font-weight: 400;">
											{{pool.totalStaking!="-" ? utils.TokenAmount(pool.stakedToken,pool.totalStaking).toFixed(3):"-"}}
											{{pool.stakedToken.symbol}}
										</span>
									</el-row>
								</el-col>
							</el-row>
							<el-row type="flex" align="middle" style="margin: 10px 0px;">
								<el-col :span="10">
									<span style="font-size: 18px;font-weight: 400;white-space:nowrap;">
										{{language[language.default].Maximumstacking}}:
									</span>
								</el-col>
								<el-col :span="14">
									<el-row type="flex" justify="end">
										<span style="font-size: 18px;font-weight: 400;">
											{{pool.poolLimitPerUser}} {{pool.stakedToken.symbol}}
										</span>
									</el-row>
								</el-col>
							</el-row>
							<el-row type="flex" align="middle" style="margin: 10px 0px;">
								<el-col :span="10">
									<span style="font-size: 18px;font-weight: 400;white-space:nowrap;">
										{{language[language.default].Endcountdown}}:
									</span>
								</el-col>
								<el-col :span="14">
									<el-row type="flex" justify="end">
										<span style="font-size: 18px;font-weight: 400;">
											{{pool.endblockNumber}} {{language[language.default].Block}}
										</span>
									</el-row>
								</el-col>
							</el-row>
							<el-row type="flex" align="middle" style="margin: 10px 0px;">
								<el-col :span="18">
									<el-row>
										<span style="font-size: 16px;font-weight: 400;">
											{{pool.rewardToken.symbol}} {{language[language.default].EARNED}}
										</span>
									</el-row>
									<el-row>
										<span style="font-size: 20px;font-weight: 600;color: rgb(189, 194, 196);">
											{{pool.pendingReward!="-" ? utils.TokenAmount(pool.rewardToken,pool.pendingReward).toSignificant():"-"}}
										</span>
									</el-row>
								</el-col>
								<el-col :span="6">
									<el-row type="flex" justify="end">
										<button v-if="pool.pendingReward>0" @click="harvest(pool)" class="but0" style="font-size: 12px;">
											{{language[language.default].Harvest}}
										</button>
										<el-tooltip v-if="pool.pendingReward==0" placement="top">
											<div slot="content">{{language[language.default].q37}}</div>
											<button class="button_disabled" style="font-size: 12px;">
												{{language[language.default].Harvest}}
											</button>
										</el-tooltip>
									</el-row>
								</el-col>
							</el-row>
							<el-row type="flex" align="middle" style="margin: 10px 0px;">
								<el-col :span="16">
									<el-row>
										<span style="font-size: 16px;font-weight: 400;">
											{{pool.stakedToken.symbol}} {{language[language.default].STAKED}}
										</span>
									</el-row>
									<el-row>
										<span style="font-size: 20px;font-weight: 600;color: rgb(189, 194, 196);">
											{{utils.TokenAmount(pool.stakedToken,pool.userInfo.amount).toSignificant()}}
										</span>
									</el-row>
								</el-col>
								<el-col :span="8">
									<el-row type="flex" justify="end">
										<el-tooltip v-if="pool.userInfo.amount == 0" placement="top">
											<div slot="content">{{language[language.default].q38}}</div>
											<button class="css_button_1" style="cursor: not-allowed;">
												<svg t="1637476614756" class="icon" viewBox="0 0 1024 1024" version="1.1"
													xmlns="http://www.w3.org/2000/svg" p-id="3810" width="48" height="48">
													<path
														d="M904 64c30.9 0 56 25.1 56 56v784c0 30.9-25.1 56-56 56H120c-30.9 0-56-25.1-56-56V120c0-30.9 25.1-56 56-56h784m0-64H120C53.7 0 0 53.7 0 120v784c0 66.3 53.7 120 120 120h784c66.3 0 120-53.7 120-120V120c0-66.3-53.7-120-120-120z"
														fill="#E9EAEB" p-id="3811"></path>
													<path
														d="M736 480H288c-17.7 0-32 14.3-32 32s14.3 32 32 32h448c17.7 0 32-14.3 32-32s-14.3-32-32-32z"
														fill="#E9EAEB" p-id="3812"></path>
												</svg>
											</button>
										</el-tooltip>
										<el-tooltip v-if="pool.allowance == 0 || pool.allowance=='-'" placement="top">
											<div slot="content">{{language[language.default].q39}}</div>
											<button class="css_button_1" style="cursor: not-allowed;">
												<svg t="1637476546059" class="icon" viewBox="0 0 1024 1024" version="1.1"
													xmlns="http://www.w3.org/2000/svg" p-id="2473" width="48" height="48">
													<path
														d="M904 64c30.9 0 56 25.1 56 56v784c0 30.9-25.1 56-56 56H120c-30.9 0-56-25.1-56-56V120c0-30.9 25.1-56 56-56h784m0-64H120C53.7 0 0 53.7 0 120v784c0 66.3 53.7 120 120 120h784c66.3 0 120-53.7 120-120V120c0-66.3-53.7-120-120-120z"
														fill="#E9EAEB" p-id="2474"></path>
													<path
														d="M736 480H288c-17.7 0-32 14.3-32 32s14.3 32 32 32h448c17.7 0 32-14.3 32-32s-14.3-32-32-32z"
														fill="#E9EAEB" p-id="2475"></path>
													<path
														d="M480 288v448c0 17.7 14.3 32 32 32s32-14.3 32-32V288c0-17.7-14.3-32-32-32s-32 14.3-32 32z"
														fill="#E9EAEB" p-id="2476"></path>
												</svg>
											</button>
										</el-tooltip>
										<button @click="openWithdraw(pool)" v-if="pool.userInfo.amount > 0"
											class="css_button_0">
											<svg t="1637476614756" class="icon" viewBox="0 0 1024 1024" version="1.1"
												xmlns="http://www.w3.org/2000/svg" p-id="3810" width="48" height="48">
												<path
													d="M904 64c30.9 0 56 25.1 56 56v784c0 30.9-25.1 56-56 56H120c-30.9 0-56-25.1-56-56V120c0-30.9 25.1-56 56-56h784m0-64H120C53.7 0 0 53.7 0 120v784c0 66.3 53.7 120 120 120h784c66.3 0 120-53.7 120-120V120c0-66.3-53.7-120-120-120z"
													fill="#FF99CC" p-id="3811"></path>
												<path
													d="M736 480H288c-17.7 0-32 14.3-32 32s14.3 32 32 32h448c17.7 0 32-14.3 32-32s-14.3-32-32-32z"
													fill="#FF99CC" p-id="3812"></path>
											</svg>
										</button>
										<button @click="openDeposit(pool)" v-if="pool.allowance > 0" class="css_button_0">
											<svg t="1637476546059" class="icon" viewBox="0 0 1024 1024" version="1.1"
												xmlns="http://www.w3.org/2000/svg" p-id="2473" width="48" height="48">
												<path
													d="M904 64c30.9 0 56 25.1 56 56v784c0 30.9-25.1 56-56 56H120c-30.9 0-56-25.1-56-56V120c0-30.9 25.1-56 56-56h784m0-64H120C53.7 0 0 53.7 0 120v784c0 66.3 53.7 120 120 120h784c66.3 0 120-53.7 120-120V120c0-66.3-53.7-120-120-120z"
													fill="#FF99CC" p-id="2474"></path>
												<path
													d="M736 480H288c-17.7 0-32 14.3-32 32s14.3 32 32 32h448c17.7 0 32-14.3 32-32s-14.3-32-32-32z"
													fill="#FF99CC" p-id="2475"></path>
												<path
													d="M480 288v448c0 17.7 14.3 32 32 32s32-14.3 32-32V288c0-17.7-14.3-32-32-32s-32 14.3-32 32z"
													fill="#FF99CC" p-id="2476"></path>
											</svg>
										</button>
									</el-row>
								</el-col>
							</el-row>
							<div>
								<el-row type="flex" align="middle">
									<button v-if="pool.allowance=='-'" class="button_disabled">
										{{language[language.default].Approve}}
									</button>
									<button v-else-if="pool.allowance > 0" class="button_disabled">
										{{language[language.default].Approved}}
									</button>
									<button v-else-if="pool.allowance == 0" class="but0" @click="approve(pool.address,pool.stakedToken.address,pool.stakedToken.symbol)">
										{{language[language.default].Approve}}
									</button>
								</el-row>
							</div>
						</el-row>
					</el-card>
				</el-col>
			</el-row>
<!-- 			<div class="el-row is-align-middle el-row--flex">
				<svg t="1637888664603" class="icon" viewBox="0 0 1024 1024"version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2969" width="32" height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m44.245333-469.333333l159.914667-159.914667a31.274667 31.274667 0 1 0-44.245333-44.245333L512 467.754667 352.085333 307.84a31.274667 31.274667 0 1 0-44.245333 44.245333L467.754667 512l-159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333 44.245333L512 556.245333l159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333-44.245333L556.245333 512z" fill="#F5222D" p-id="2970"></path></svg>
				<span style="margin-left: 5px;font-weight: 700;">Gas</span>
			</div>
			<div class="el-row is-align-middle el-row--flex">
				<svg t="1637976838785" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2496" width="32" height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m-50.432-326.101333L310.613333 504.32a32 32 0 0 0-45.226666 45.226667l174.72 174.762666a32.341333 32.341333 0 0 0 0.341333 0.341334l0.256 0.213333a32 32 0 0 0 50.048-6.144l337.450667-379.605333a32 32 0 1 0-47.872-42.496l-318.762667 358.613333z" fill="#52C41A" p-id="2497"></path></svg>
				<span style="margin-left: 5px;font-weight: 700;"></span>
			</div> -->
		</el-row>
	</el-row>
</template>

<script>
	export default {
		props:{account:Object},
		data() {
			return {
				withdrawDialogData: {
					Bool: false,
					Input: "",
					Pool: {stakedToken:{symbol:""}},
					value: 0
				},
				depositDialogData: {
					Bool: false,
					Input: "",
					Pool: {stakedToken:{symbol:""}},
					value: 0
				},
				input: "",
				marks: {
					0: {
						style: {
							'white-space': 'nowrap',
							'margin': '-25px 0px 0px 0px'
						},
						label: this.$createElement('strong', '0%')
					},
					25: {
						style: {
							'white-space': 'nowrap',
							'margin': '-25px 0px 0px 0px'
						},
						label: this.$createElement('strong', '25%')
					},
					50: {
						style: {
							'white-space': 'nowrap',
							'margin': '-25px 0px 0px 0px'
						},
						label: this.$createElement('strong', '50%')
					},
					75: {
						style: {
							'white-space': 'nowrap',
							'margin': '-25px 0px 0px 0px'
						},
						label: this.$createElement('strong', '75%')
					},
					100: {
						style: {
							'white-space': 'nowrap',
							'margin': '-25px 0px 0px 0px'
						},
						label: this.$createElement('strong', '100%')
					}
				},
				pools: [{
					address: "0xc28c400F2B675b25894FA632205ddec71E432288",
					apr: "-",
					poolLimitPerUser: "-",
					totalStaking: "-",
					pendingReward: "-",
					allowance: "-",
					endblockNumber: "-",
					userInfo: {
						amount: 0,
						rewardDebt: 0
					},
					stakedToken: {
						address: "0x0E09FaBB73Bd3Ade0a17ECC321fD13a19e81cE82",
						symbol: "CAKE",
						decimals: 18,
						img: "data:image/svg+xml;base64,"
					},
					rewardToken: {
						address: "0xD44FD09d74cd13838F137B590497595d6b3FEeA4",
						symbol: "ETERNAL",
						decimals: 18,
						img: "data:image/svg+xml;base64,"
					}
				}, {
					address: "0x9b861A078B2583373A7a3EEf815bE1A39125Ae08",
					apr: "-",
					poolLimitPerUser: "-",
					totalStaking: "-",
					pendingReward: "-",
					allowance: "-",
					endblockNumber: "-",
					userInfo: {
						amount: 0,
						rewardDebt: 0
					},
					stakedToken: {
						address: "0x0E09FaBB73Bd3Ade0a17ECC321fD13a19e81cE82",
						symbol: "CAKE",
						decimals: 18,
						img: "data:image/svg+xml;base64,"
					},
					rewardToken: {
						address: "0x23CE9e926048273eF83be0A3A8Ba9Cb6D45cd978",
						symbol: "DAR",
						decimals: 6,
						img: "data:image/svg+xml;base64,"
					}
				}, {
					address: "0x79f5f7DDADeFa0A9e850DFFC4fBa77e5172Fe701",
					apr: "-",
					poolLimitPerUser: "-",
					totalStaking: "-",
					pendingReward: "-",
					allowance: "-",
					endblockNumber: "-",
					userInfo: {
						amount: 0,
						rewardDebt: 0
					},
					stakedToken: {
						address: "0x0E09FaBB73Bd3Ade0a17ECC321fD13a19e81cE82",
						symbol: "CAKE",
						decimals: 18,
						img: "data:image/svg+xml;base64,"
					},
					rewardToken: {
						address: "0x6b23C89196DeB721e6Fd9726E6C76E4810a464bc",
						symbol: "XWG",
						decimals: 18,
						img: "data:image/svg+xml;base64,"
					}
				}]
			}
		},
		created() {
			var temp_this = this
			window.setInterval(function() {
				if(temp_this.account.address.length == 42){
					temp_this.SmartChef.getPoolData(temp_this.pools, temp_this.account.address, function(ret) {
						for (var i = 0; i < temp_this.pools.length; i++) {
							temp_this.pools[i].apr = ret[i].apr
							temp_this.pools[i].pendingReward = ret[i].pendingReward
							temp_this.pools[i].userInfo = ret[i].userInfo
							temp_this.pools[i].totalStaking = ret[i].totalStaking
							temp_this.pools[i].endblockNumber = ret[i].endblockNumber
							temp_this.pools[i].allowance = ret[i].allowance
							temp_this.pools[i].poolLimitPerUser = ret[i].poolLimitPerUser == 0 ? "-" : ret[i].poolLimitPerUser
						}
						//console.log(temp_this.pools)
					})
				}
			}, 1000 * 3);
		},
		methods: {
			m() {

			},
			success(msg){
				this.$notify({
					title: '',
					dangerouslyUseHTMLString: true,
					message: '<div class="el-row is-align-middle el-row--flex"><svg t="1637976838785"class="icon"viewBox="0 0 1024 1024"version="1.1"xmlns="http://www.w3.org/2000/svg"p-id="2496"width="32"height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m-50.432-326.101333L310.613333 504.32a32 32 0 0 0-45.226666 45.226667l174.72 174.762666a32.341333 32.341333 0 0 0 0.341333 0.341334l0.256 0.213333a32 32 0 0 0 50.048-6.144l337.450667-379.605333a32 32 0 1 0-47.872-42.496l-318.762667 358.613333z"fill="#52C41A"p-id="2497"></path></svg><span style="margin-left: 5px;font-weight: 700;">'+msg+'</span></div>'
				});
			},
			fail(msg){
				this.$notify({
					title: '',
					dangerouslyUseHTMLString: true,
					message: '<div class="el-row is-align-middle el-row--flex"><svg t="1637888664603"class="icon"viewBox="0 0 1024 1024"version="1.1"xmlns="http://www.w3.org/2000/svg"p-id="2969"width="32"height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m44.245333-469.333333l159.914667-159.914667a31.274667 31.274667 0 1 0-44.245333-44.245333L512 467.754667 352.085333 307.84a31.274667 31.274667 0 1 0-44.245333 44.245333L467.754667 512l-159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333 44.245333L512 556.245333l159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333-44.245333L556.245333 512z"fill="#F5222D"p-id="2970"></path></svg><span style="margin-left: 5px;font-weight: 700;">'+msg+'</span></div>'
				});
			},
			approve(spender,token,symbol){
				var temp_this = this
				this.$emit("executionWindow",true,"Approve the use of your "+symbol)
				this.Wallet.approve(spender,this.account.address,token,function(err,ret){
					if (!err) {
						temp_this.success(temp_this.language[temp_this.language.default].q42)
					}else{
						temp_this.fail(temp_this.language[temp_this.language.default].q43)
					}
					temp_this.$emit("executionWindow",false,"")
				})
			},
			openWithdraw(pool) {
				this.withdrawDialogData.Pool = pool
				this.withdrawDialogData.Bool = true
				//console.log(pool)
			},
			openDeposit(pool) {
				this.depositDialogData.Pool = pool
				this.depositDialogData.Bool = true
				//console.log(pool)
			},
			withdrawDialogChange(e) {
				var amount = new this.BigNumber(this.withdrawDialogData.Pool.userInfo.amount).multipliedBy(e * 100).div(10000).toFixed(0)
				this.withdrawDialogData.Input = this.utils.TokenAmount(this.withdrawDialogData.Pool.stakedToken, amount).toSignificant(30)
			},
			depositDialogChange(e) {
				var amount = new this.BigNumber(this.account.balances[this.depositDialogData.Pool.stakedToken.address]).multipliedBy(e * 100).div(10000).toFixed(0)
				this.depositDialogData.Input = this.utils.TokenAmount(this.depositDialogData.Pool.stakedToken, amount).toSignificant(30)
			},
			withdraw() {
				var temp_this = this
				this.$emit("executionWindow",true,"Withdraw "+this.withdrawDialogData.Input+" "+this.withdrawDialogData.Pool.stakedToken.symbol)
				var amount = new this.BigNumber(this.withdrawDialogData.Input).multipliedBy(10**this.withdrawDialogData.Pool.stakedToken.decimals).toString(10)
				this.SmartChef.withdraw(this.account, this.withdrawDialogData.Pool, amount, function(err, ret) {
					if (!err) {
						temp_this.success(temp_this.language[temp_this.language.default].q42)
					}else{
						temp_this.fail(temp_this.language[temp_this.language.default].q43)
					}
					temp_this.$emit("executionWindow",false,"")
				})
			},
			deposit() {
				var temp_this = this
				this.$emit("executionWindow",true,"Deposit "+this.depositDialogData.Input+" "+this.depositDialogData.Pool.stakedToken.symbol)
				var amount = new this.BigNumber(this.depositDialogData.Input).multipliedBy(10**this.depositDialogData.Pool.stakedToken.decimals).toString(10)
				this.SmartChef.deposit(this.account, this.depositDialogData.Pool, amount, function(err, ret) {
					if (!err) {
						temp_this.success(temp_this.language[temp_this.language.default].q42)
					}else{
						temp_this.fail(temp_this.language[temp_this.language.default].q43)
					}
					temp_this.$emit("executionWindow",false,"")
				})
			},
			harvest(pool){
				var temp_this = this
				this.$emit("executionWindow",true,"Harvest "+pool.rewardToken.symbol)
				this.SmartChef.deposit(this.account, pool, 0, function(err, ret) {
					if (!err) {
						temp_this.success(temp_this.language[temp_this.language.default].q42)
					}else{
						temp_this.fail(temp_this.language[temp_this.language.default].q43)
					}
					temp_this.$emit("executionWindow",false,"")
				})
			}
		}
	}
</script>

