<template>
	<el-row>
		<el-row style="padding: 150px 0px;background-color: transparent;">
			<el-dialog :visible.sync="tokenSelectBool">
				<el-row>
					<el-row style="padding: 10px;">
						<input v-model="c" class="input_1" placeholder="Search name or paste address"></input>
					</el-row>
					<div style="margin-top: 20px;padding: 10px;overflow:auto;height: 50vh;">
						<el-row v-for="token in tokenlist" v-bind:key="token.symbol">
							<button @click="tokenSelect(token)" class="css_button_0" style="width: 100%;">
								<el-row type="flex" align="middle">
									<el-col :span="12">
										<el-row type="flex" align="middle">
											<img width="36" height="36" :src="token.img">
											<h3 style="margin: 0 10px;">{{token.symbol}}</h3>
										</el-row>
									</el-col>
									<el-col :span="12">
										<el-row type="flex" justify="end" style="width: 100%;">{{account.balances[token.address]?utils.TokenAmount(token,account.balances[token.address]).toSignificant():0}}</el-row>
									</el-col>
								</el-row>
							</button>
							<el-row type="flex" align="middle">
								<div class="line"></div>
							</el-row>
						</el-row>
					</div>
				</el-row>
			</el-dialog>
			<el-row type="flex" justify="center" style="background-color: transparent;">
				<el-col :xs="22" :sm="12" :md="12" :lg="8" :xl="8">
					<el-row style="margin-bottom: 28px;">
						<el-carousel height="150px">
							<el-carousel-item v-for="item in 1" :key="item">
								<el-row type="flex" align="middle" justify="center" style="height: 150px;padding: 1px;">
									<el-image :src="img_0">
										<div slot="error" class="image-slot">
											<i class="el-icon-picture-outline"></i>
										</div>
									</el-image>
								</el-row>
							</el-carousel-item>
						</el-carousel>
					</el-row>
					<el-card shadow="never" style="border-radius: 35px;border: 1px solid rgb(231, 227, 235);" :body-style="{ padding: '10px' }">
						<el-row style="margin: 20px;margin-top: 0px;">
							<el-row type="flex" align="middle">
								<el-col :span="12">
									<h3 style="color: #993399;">{{language[language.default].Swap}}</h3>
								</el-col>
								<el-col :span="12">
									<el-row type="flex" justify="end">
										<button class="css_button_0" @click="openSettings">
											<i class="el-icon-s-tools" style="font-size: 28px;margin: 5px;color: #993399;"></i>
										</button>
									</el-row>
								</el-col>
							</el-row>
						</el-row>
						<el-divider></el-divider>
						<el-row style="margin: 20px;">
							<el-col :span="10">
								<el-row type="flex">
									<button class="css_button_0" @click="tokenSelectMode = 1;tokenSelectBool = true">
										<el-row type="flex" align="middle">
											<img width="36" height="36" :src="form.img">
											<h3>{{form.symbol}}</h3>
											<i class="el-icon-arrow-down"></i>
										</el-row>
									</button>
								</el-row>
								<el-row type="flex">
									Balance:{{account.balances[form.address]?utils.TokenAmount(form,account.balances[form.address]).toSignificant():0}}
								</el-row>
							</el-col>
							<el-col :span="4">
								<el-row type="flex" justify="center" style="margin-top: 120px;">
									<button class="css_button_0" @click="m()">
										<svg t="1632986035388" class="icon" viewBox="0 0 1024 1024" version="1.1"
											xmlns="http://www.w3.org/2000/svg" p-id="2465" width="16" height="16">
											<path
												d="M987.392 861.184c-18.432 0-33.28-14.848-33.28-33.28v-587.52c0-36.736-29.824-66.56-66.56-66.56H458.496c-18.432 0-33.28-14.848-33.28-33.28s14.848-33.28 33.28-33.28h429.056c73.344 0 132.992 59.648 132.992 132.992v587.648c0.128 18.432-14.72 33.28-33.152 33.28z"
												fill="#993399" p-id="2466"></path>
											<path d="M223.616 140.544l331.008 135.296-78.592-135.296L554.624 5.376z"
												fill="#993399" p-id="2467"></path>
											<path
												d="M565.504 916.736H136.448c-73.344 0-132.992-59.648-132.992-132.992V195.968c0-18.432 14.848-33.28 33.28-33.28s33.28 14.848 33.28 33.28v587.648c0 36.736 29.824 66.56 66.56 66.56h429.056c18.432 0 33.28 14.848 33.28 33.28-0.128 18.432-15.104 33.28-33.408 33.28z"
												fill="#993399" p-id="2468"></path>
											<path d="M800.384 883.456L469.376 748.16l78.592 135.296-78.592 135.168z"
												fill="#993399" p-id="2469"></path>
										</svg>
									</button>
								</el-row>
							</el-col>
							<el-col :span="10">
								<el-row type="flex" justify="end">
									<button class="css_button_0" @click="tokenSelectMode = 2;tokenSelectBool = true">
										<el-row type="flex" align="middle">
											<img width="36" height="36" :src="to.img">
											<h3>{{to.symbol}}</h3>
											<i class="el-icon-arrow-down"></i>
										</el-row>
									</button>
								</el-row>
								<el-row type="flex" justify="end">
									Balance:{{account.balances[to.address]?utils.TokenAmount(to,account.balances[to.address]).toSignificant():0}}
								</el-row>
							</el-col>
						</el-row>
						<el-row style="margin: 20px;">
							<el-col :span="12" type="flex">
								<input @input="form_input_event" v-model="form_input" min="0.0" class="input_0"
									placeholder="0.0" style="text-align: left;" />
							</el-col>
							<el-col :span="12" type="flex" justify="end">
								<input @input="to_input_event" v-model="to_input" min="0.0" class="input_0"
									placeholder="0.0" style="text-align: right;" />
							</el-col>
						</el-row>
						<el-row style="margin: 20px;">
							<button v-if="account.address.length != 42" @click="connectWallet" class="but0">{{language[language.default].Connectwallet}}</button>
							<button v-else-if="form_input == 0 && to_input == 0" class="button_disabled">{{language[language.default].Enteranamount}}</button>
							<button v-else-if="data0.route == null" class="button_disabled">{{language[language.default].Insufficientliquidity}}</button>
							<button v-else-if="utils.TokenAmount(form,account.balances[form.address]).toSignificant(30) < form_input" class="button_disabled">{{language[language.default].Insufficientbalance}}</button>
							<button v-else-if="account.allowances[form.address] == 0 && utils.TokenAmount(form,account.balances[form.address]).toSignificant(30) >= form_input" @click="approve" class="but0">{{language[language.default].Approve}}</button>
							<button v-else-if="account.allowances[form.address] > 0 && utils.TokenAmount(form,account.balances[form.address]).toSignificant(30) >= form_input" @click="swap" class="but0">{{language[language.default].Exchange}}</button>
						</el-row>
						<el-divider v-if="data0.route!='' && data0.route!=null"></el-divider>
						<el-row v-if="data0.route!='' && data0.route!=null" style="margin: 20px;">
							<el-row>
								<el-col :span="6">
									{{language[language.default].Slippage}}
								</el-col>
								<el-col :span="18">
									<el-row type="flex" justify="end">
										{{account.slipPoint}} %
									</el-row>
								</el-col>
							</el-row>
							<el-row v-if="data0.type == 'OUT'">
								<el-col :span="6" style="white-space:nowrap;">
									{{language[language.default].Minimumreceived}}
								</el-col>
								<el-col :span="18">
									<el-row type="flex" justify="end">
										{{data0.miniAmount}} {{to.symbol}}
									</el-row>
								</el-col>
							</el-row>
							<el-row v-if="data0.type == 'IN'">
								<el-col :span="6" style="white-space:nowrap;">
									{{language[language.default].Maximumsold}}
								</el-col>
								<el-col :span="18">
									<el-row type="flex" justify="end">
										{{data0.maxAmount}} {{form.symbol}}
									</el-row>
								</el-col>
							</el-row>
							<el-row>
								<el-col :span="6" style="white-space:nowrap;">
									{{language[language.default].Priceimpact}}
								</el-col>
								<el-col :span="18">
									<el-row type="flex" justify="end">
										{{data0.priceImpact}} %
									</el-row>
								</el-col>
							</el-row>
							<el-row>
								<el-col :span="6" style="white-space:nowrap;">
									{{language[language.default].LiquidityProviderFee}}
								</el-col>
								<el-col :span="18">
									<el-row type="flex" justify="end">
										{{data0.liquidityProviderFee}} {{form.symbol}}
									</el-row>
								</el-col>
							</el-row>
							<!-- <el-row>
								<el-col :span="6">
									
								</el-col>
								<el-col :span="18">
									<el-row type="flex" justify="end">
										{{data0.reward}} choco
									</el-row>
								</el-col>
							</el-row> -->
							<el-row type="flex" justify="center" style="padding: 20px 0px;">
								{{data0.route}}
							</el-row>
						</el-row>
					</el-card>
				</el-col>
			</el-row>
		</el-row>
	</el-row>
</template>

<script>
	import tokenlist from '../assets/tokenlist.json'
	export default {
		props:{account:Object},
		data() {
			return {
				img_0:"data:image/jpg;base64,",
				c: "",
				tokenSelectMode: 0,
				tokenSelectBool: false,
				tokenlist: tokenlist,
				form_input: "",
				form_balance: 0,
				to_input: "",
				to_balance: 0,
				form: tokenlist[0],
				to: tokenlist[2],
				data0: {},
				lastInput:""
			}
		},
		created() {
			var temp_this = this
			window.setInterval(function(){
				if(temp_this.lastInput == "form_input_event"){
					temp_this.form_input_event()
				}else if(temp_this.lastInput == "to_input_event"){
					temp_this.to_input_event()
				}
			},1000 * 5);
		},
		methods: {
			tokenSelect(e) {
				if (this.tokenSelectMode == 1) {
					if (e.address != this.to.address) {
						this.form = e
					} else {
						this.m()
					}
				} else {
					if (e.address != this.form.address) {
						this.to = e
					} else {
						this.m()
					}
				}
				this.form_input_event()
				this.tokenSelectMode = 0
				this.tokenSelectBool = false
			},
			m() {
				var a = this.form
				this.form = this.to
				this.to = a
				this.form_input_event()
			},
			swap() {
				var temp_this = this
				this.$emit("executionWindow",true,"Swapping "+this.form_input+" "+this.form.symbol+" for "+this.to_input+" "+this.to.symbol)
				this.data0.call(function(err,ret){
					if (!err) {
						temp_this.success(temp_this.language[temp_this.language.default].q42)
					}else{
						temp_this.fail(temp_this.language[temp_this.language.default].q43)
					}
					temp_this.$emit("executionWindow",false,"")
				})
			},
			connectWallet(){
				this.$emit("DataEvent","connectListBool",true)
			},
			openSettings(){
				this.$emit("DataEvent","settingsBool",true)
			},
			success(msg){
				this.$notify({
					title: '',
					dangerouslyUseHTMLString: true,
					message: '<div class="el-row is-align-middle el-row--flex"><svg t="1637976838785"class="icon"viewBox="0 0 1024 1024"version="1.1"xmlns="http://www.w3.org/2000/svg"p-id="2496"width="32"height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m-50.432-326.101333L310.613333 504.32a32 32 0 0 0-45.226666 45.226667l174.72 174.762666a32.341333 32.341333 0 0 0 0.341333 0.341334l0.256 0.213333a32 32 0 0 0 50.048-6.144l337.450667-379.605333a32 32 0 1 0-47.872-42.496l-318.762667 358.613333z"fill="#52C41A"p-id="2497"></path></svg><span style="margin-left: 5px;font-weight: 700;">'+msg+'</span></div>'
				});
			},
			fail(msg){
				this.$notify({
					title: '',
					dangerouslyUseHTMLString: true,
					message: '<div class="el-row is-align-middle el-row--flex"><svg t="1637888664603"class="icon"viewBox="0 0 1024 1024"version="1.1"xmlns="http://www.w3.org/2000/svg"p-id="2969"width="32"height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m44.245333-469.333333l159.914667-159.914667a31.274667 31.274667 0 1 0-44.245333-44.245333L512 467.754667 352.085333 307.84a31.274667 31.274667 0 1 0-44.245333 44.245333L467.754667 512l-159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333 44.245333L512 556.245333l159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333-44.245333L556.245333 512z"fill="#F5222D"p-id="2970"></path></svg><span style="margin-left: 5px;font-weight: 700;">'+msg+'</span></div>'
				});
			},
			approve(){
				var temp_this = this
				this.$emit("executionWindow",true,"Approve the use of your "+this.form.symbol)
				this.Wallet.approve("0x50ae49971c8Ffd3ADdFFd7adc8C662069ea154eE",this.account.address,this.form.address,function(err,ret){
					if (!err) {
						temp_this.success(temp_this.language[temp_this.language.default].q42)
					}else{
						temp_this.fail(temp_this.language[temp_this.language.default].q43)
					}
					temp_this.$emit("executionWindow",false,"")
				})
			},
			form_input_event() {
				if (this.form_input == "" || this.form_input<0.000000000000000001) {
					this.data0 = {}
					this.to_input = ""
					this.lastInput = ""
					return
				}
				var tokenIn = this.form
				var tokenOut = this.to
				var amountIn = this.form_input
				var data = this.SwapTools.bestPathOut(this.account,tokenIn, tokenOut, new this.BigNumber(amountIn).multipliedBy(10**tokenIn.decimals).toString(10))
				if(data!=null){
					this.to_input = data.amount
					this.data0 = data
				}
				this.lastInput = "form_input_event"
			},
			to_input_event() {
				if (this.to_input == "" || this.to_input<0.000000000000000001) {
					this.data0 = {}
					this.form_input = ""
					this.lastInput = ""
					return
				}
				var tokenIn = this.form
				var tokenOut = this.to
				var amountOut = this.to_input
				var data = this.SwapTools.bestPathIn(this.account,tokenIn, tokenOut, new this.BigNumber(amountOut).multipliedBy(10**tokenOut.decimals).toString(10))
				if(data!=null){
					this.form_input = data.amount
					this.data0 = data
				}
				this.lastInput = "to_input_event"
			}
		}
	}
</script>

<style>
	.input_0 {
		width: 100%;
		text-align: right;
		padding: 8px;
		border: none;
		background: transparent;
		padding-left: 0;
		font-size: 24px;
	}

	.input_0:focus {
		outline: none;
	}

	.input_1 {
		width: 100%;
		text-align: center;
		padding: 10px 0px;
		border: 1px solid #d9d9d9;
		background: transparent;
		font-size: 20px;
		border-radius: 25px;
	}

	.input_1:focus {
		outline: none;
		border: 1px solid #FF99CC;
	}

	.line {
		height: 1px;
		margin: 10px;
		border-top: 1px solid #ddd;
		text-align: center;
		width: 100%;
	}
	.el-dialog__wrapper > .el-dialog{
		min-width: 380px;
		border-radius: 35px;
	}
</style>
