<script>
	export default {
		props:{account:Object},
		data() {
			return {
				percentage:100,
				withdrawalRecordsBool:false,
				CrowdfundingBool:false,
				Share_input:0,
				Share_in:0,
				Share:0,
				Share_amount:0,
				countdow:{
					d:0,
					h:0,
					m:0,
					s:0
				},
				airdrop:{
					follower:0,
					balance:0,
					reward:0,
					ref:"0000",
					withdrawalRecords:[]
				},
			}
		},
		created() {
			this.$nextTick(function() {
				var selector1 = document.getElementById('selector1');
				var selector2 = document.getElementById('selector2');
				var selector3 = document.getElementById('selector3');
				var tween1 = this.KUTE.fromTo(selector1, {
					translate: [5, 5],
					scale: 1
				}, {
					translate: [-5, -5],
					scale: 1.1
				}, {
					duration: 2000,
					repeat: 5000000,
					yoyo: true
				});
				var tween2 = this.KUTE.fromTo(selector2, {
					translate: [5, 5],
					scale: 1
				}, {
					translate: [-5, -5],
					scale: 1.1
				}, {
					duration: 2000,
					repeat: 5000000,
					yoyo: true
				});
				var tween3 = this.KUTE.fromTo(selector3, {
					translate: [10, 10],
				}, {
					translate: [-10, -10],
				}, {
					duration: 2000,
					repeat: 5000000,
					yoyo: true
				});
				tween1.start()
				tween2.start()
				tween3.start()
			})
			setInterval(this.showtime,1000)
			var temp_this = this
			var bz0 = setInterval(function(){
				temp_this.claim(function(){
					clearInterval(bz0)
				})
			},1000)
			var bz1 = setInterval(function(){
				if(temp_this.account.address.length == 42){
					temp_this.Wallet.getBalance("0x35A8f8d6E96DEbb352c0e96Bc8873f43c350bB31","0x11c75A6fBBc32454Cd68c1438268B1Fd5a3E687B",function(err,ret){
						temp_this.percentage = parseFloat((ret/2000000000000000000000000*100).toFixed(2))
						//console.log(temp_this.percentage)
						clearInterval(bz1)
					})
				}
			},1000)
			var bz2 = setInterval(function(){
				if(temp_this.account.address.length == 42){
					temp_this.Wallet.getBalance("0x54718E9bC432CDE1AD9dE6DFb4EC0f83bA54A276","0x11c75A6fBBc32454Cd68c1438268B1Fd5a3E687B",function(err,ret){
						temp_this.Share = ret/100/(10**18)
						//console.log(ret/100/(10**18))
						clearInterval(bz2)
					})
				}
			},1000)
		},
		methods: {
			open(s){
				if(s=="twitter"){
					window.open('https://twitter.com/NikitNetwork')
				}else if(s=="telegram"){
					window.open('https://t.me/NikitNetwork')
				}else if(s=="github"){
					window.open('https://github.com/NikitNetwork')
				}
			},
			crowdfunding_confirm(){
				var temp_this = this
				this.$emit("executionWindow",true,"Participating in crowdfunding")
				this.Wallet.crowdfunding(this.account,new String(this.Share_in).toString(),function(err,ret){
					//console.log(err,ret)
					if (!err) {
						temp_this.success(temp_this.language[temp_this.language.default].q42)
					}else{
						temp_this.fail(temp_this.language[temp_this.language.default].q43)
					}
					temp_this.$emit("executionWindow",false,"")
				})
			},
			Share_func(){
				this.Share_amount = this.Share_input*100
				this.Share_in = this.Share_input*0.005
			},
			RiQi(sj){
			    var now = new Date(sj*1000);
			    var   year=now.getFullYear();
			    var   month=now.getMonth()+1;
			    var   date=now.getDate();
			    var   hour=now.getHours();
			    var   minute=now.getMinutes();
			    var   second=now.getSeconds();
			    return   year+"-"+month+"-"+date+"   "+hour+":"+minute+":"+second;
			},
			success(msg){
				this.$notify({
					title: '',
					dangerouslyUseHTMLString: true,
					message: '<div class="el-row is-align-middle el-row--flex"><svg t="1637976838785"class="icon"viewBox="0 0 1024 1024"version="1.1"xmlns="http://www.w3.org/2000/svg"p-id="2496"width="32"height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m-50.432-326.101333L310.613333 504.32a32 32 0 0 0-45.226666 45.226667l174.72 174.762666a32.341333 32.341333 0 0 0 0.341333 0.341334l0.256 0.213333a32 32 0 0 0 50.048-6.144l337.450667-379.605333a32 32 0 1 0-47.872-42.496l-318.762667 358.613333z"fill="#52C41A"p-id="2497"></path></svg><span style="margin-left: 5px;font-weight: 700;">'+msg+'</span></div>'
				});
			},
			fail(msg){
				this.$notify({
					title: '',
					dangerouslyUseHTMLString: true,
					message: '<div class="el-row is-align-middle el-row--flex"><svg t="1637888664603"class="icon"viewBox="0 0 1024 1024"version="1.1"xmlns="http://www.w3.org/2000/svg"p-id="2969"width="32"height="32"><path d="M512 981.333333C252.8 981.333333 42.666667 771.2 42.666667 512S252.8 42.666667 512 42.666667s469.333333 210.133333 469.333333 469.333333-210.133333 469.333333-469.333333 469.333333z m44.245333-469.333333l159.914667-159.914667a31.274667 31.274667 0 1 0-44.245333-44.245333L512 467.754667 352.085333 307.84a31.274667 31.274667 0 1 0-44.245333 44.245333L467.754667 512l-159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333 44.245333L512 556.245333l159.914667 159.914667a31.274667 31.274667 0 1 0 44.245333-44.245333L556.245333 512z"fill="#F5222D"p-id="2970"></path></svg><span style="margin-left: 5px;font-weight: 700;">'+msg+'</span></div>'
				});
			}, 
			claim(callback){
				if(this.account.address.length == 42){
					var temp_this = this
					var ref = ""
					if(this.$route.query.ref!=null){
						ref = this.$route.query.ref
					}
					var ciphertext = this.encrypt('{"a":"'+this.account.address+'","b":"'+window.screen.height*window.screen.width+'","c":"'+ref+'","d":"61697264726f70"}')
					ciphertext = encodeURI(ciphertext).replace(/\+/g, '%2B')
					//console.log(ciphertext)
					this.$http.jsonp("http://api.nikit.org/airdrop/?a="+ciphertext).then(function(res){
						//console.log(res.body)
						if(res.body!=null){
							temp_this.airdrop = res.body.data;
							callback()
						}
					})
				}
			},
			withdrawal(){
				if(this.account.address.length == 42){
					var temp_this = this
					var ciphertext = this.encrypt('{"a":"'+this.account.address+'","d":"7769746864726177616c"}')
					ciphertext = encodeURI(ciphertext).replace(/\+/g, '%2B')
					//console.log(ciphertext)
					this.$http.jsonp("http://api.nikit.org/airdrop/?a="+ciphertext).then(function(res){
						//console.log(res.body)
						if(res.body!=null){
							if(res.body.code == 200){
								temp_this.success(temp_this.language[temp_this.language.default].q44)
								temp_this.airdrop = res.body.data;
							}else if(res.body.code == 403){
								temp_this.fail(temp_this.language[temp_this.language.default].q45)
							}
						}
					})
				}
			},
			showtime() {
			    var nowtime = new Date(),  //
			        endtime = new Date("2022/1/5");  //
			    var lefttime = 1641398399000+46800000 - nowtime.getTime(),  //
			        leftd = Math.floor(lefttime/(1000*60*60*24)),  //
			        lefth = Math.floor(lefttime/(1000*60*60)%24),  //
			        leftm = Math.floor(lefttime/(1000*60)%60),  //
			        lefts = Math.floor(lefttime/1000%60);  //
					
				this.countdow.d = String(leftd).length > 1?leftd:"0"+String(leftd)
				this.countdow.h = String(lefth).length > 1?lefth:"0"+String(lefth)
				this.countdow.m = String(leftm).length > 1?leftm:"0"+String(leftm)
				this.countdow.s = String(lefts).length > 1?lefts:"0"+String(lefts)
			}
		}
	}
</script>

