		font-style: italic;
		line-height: 1.5em;
	}

	.direction-r .desc {
		margin: 1em 0 0 0.75em;
	}

	/* ================ Timeline Media Queries ================ */

	@media screen and (max-width: 660px) {

		.timeline {
			width: 100%;
			padding: 4em 0 1em 0;
		}

		.timeline li {
			padding: 2em 0;
		}

