<script>
	export default {
		data(){
			return{
				img_0:"data:image/gif;base64,",
				connectListBool:false,
				walletBool:false,
				settingsBool:false,
				multiHopSwitch:true,
				executionWindowBool:false,
				executionWindowMsg:"Swapping 0.006 BNB for 0.300263 CAKE",
				account:{
					address:"",
					gasPrice:"5000000000",
					slipPoint:"1.0",
					deadline:"20",
					multiHop:true,
					balances:{},
					allowances:{}
				}
			}
		},
		created(){
			var temp_this = this
			this.getAccounts();
			window.setInterval(this.getAccounts,1000 * 5);
		},
		methods:{
			open(s){
				if(s=="twitter"){
					window.open('https://twitter.com/NikitNetwork')
				}else if(s=="telegram"){
					window.open('https://t.me/NikitNetwork')
				}else if(s=="github"){
					window.open('https://github.com/NikitNetwork')
				}
			},
			ethereum_enable(){
				window.ethereum.enable();
				this.connectListBool=false;
			},
			replace_account(){
				this.Metamask.requestAccounts(function(err,ret){
					
				})
			},
			getAccounts(){
				var temp_this = this
				this.web3.eth.getAccounts(function(err,ret){
					//console.log(ret)
					if(ret!=null && ret.length>0){
						temp_this.account.address = ret[0]
						temp_this.SwapTools.getPairs()
						temp_this.Wallet.getAllowances(ret[0],function(data){
							temp_this.account.allowances = data
						})
						temp_this.Wallet.getBalances(ret[0],function(data){
							temp_this.account.balances = data
						})
					}else{
						temp_this.account.address = ""
						temp_this.account.balances = {}
						temp_this.account.allowances = {}
					}
				})
			},
			DataEvent(name,value){
				this[name] = value
			},
			executionWindow(show,msg){
				this.executionWindowBool = show
				this.executionWindowMsg = msg
			},
			ViewBSC(){
				window.open('https://bscscan.com/address/'+this.account.address)
			},
			editGasPrice(gas){
				this.account.gasPrice = gas
			},
			editSlipPoint(slipPoint){
				this.account.slipPoint = slipPoint
			}
		}
	}
</script>

